<template>
	<view class="container" v-if="newsDetailsData.title">
		<view class="title">{{newsDetailsData.title}}</view>
		<view class="info">
			<text class="writer" decode="true">
				作者：{{newsDetailsData.writer | formatWriter}}
			</text>
			<view class="newstime">
				{{newsDetailsData.newstime | formatDate}}
			</view>
		</view>
		<view class="guide">
			编辑导语：{{newsDetailsData.smalltext}}
		</view>
		<view class="content">
			<mp-html ref="article" domain="https://www.shangzhixiao.com" :content="html" />
		</view>
		<DetailsSuspend />
	</view>

</template>

<script>
	import {
		getNewsDetails
	} from '@/api/news.js'
	import DetailsSuspend from '@/components/library/DetailsSuspend.vue'
	import {
		websiteUrl
	} from '@/utils/config.js'
	export default {
		components: {
			DetailsSuspend
		},
		data() {
			return {
				id: '',
				newsDetailsData: {},
				html: ''
			}
		},
		filters: {
			formatDate(value) {
				if (value == undefined) {
					return;
				}
				let date = new Date(value * 1000);
				// let date = new Date(value);
				//时间戳为10位需*1000，时间戳为13位的话不需乘1000
				let y = date.getFullYear();
				let MM = date.getMonth() + 1;
				MM = MM < 10 ? ('0' + MM) : MM; //月补0
				let d = date.getDate();
				d = d < 10 ? ('0' + d) : d; //天补0
				let h = date.getHours();
				h = h < 10 ? ('0' + h) : h; //小时补0
				let m = date.getMinutes();
				m = m < 10 ? ('0' + m) : m; //分钟补0
				let s = date.getSeconds();
				s = s < 10 ? ('0' + s) : s; //秒补0
				// return y + '-' + MM + '-' + d; //年月日
				return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s; //年月日时分秒
			}
			,

			formatWriter(value) {
				// #ifdef MP-WEIXIN
				return value.split('&#')[0]
				// #endif
				return value
			}

		},
		onLoad(e) {
			getNewsDetails(e.id).then(res => {
				this.newsDetailsData = res.data
				this.html = res.data.newstext.replace(/[\\]/g, '')
				let ctx = this.$refs.article
				uni.setNavigationBarTitle({
					title: res.data.title
				})
			})
		},
		onShareAppMessage(res) {

			if (res.from === 'botton') {
				console.log(res.target);
			}
			let pages = getCurrentPages() // 获取栈实例
			let currentRoute = pages[pages.length - 1].route; // 获取当前页面路由
			let currentPage = pages[pages.length - 1]['$page']['fullPath']
			return {
				title: this.newsDetailsData.title,
				path: currentPage,
				imageUrl: websiteUrl + this.newsDetailsData.titlepic,
				content: this.newsDetailsData.smalltext,
				desc: this.newsDetailsData.smalltext
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #fff;
	}

	.container {
		padding: 10px 10px 45px 10px;
		margin-bottom: 70px;

		.title {
			font-size: 18px;
			font-weight: 700;
			margin-bottom: 20px;
		}

		.info {
			display: flex;
			justify-content: space-between;
			font-size: 12px;
			color: #a6a2a2;
		}

		.guide {
			background-color: #f7f6f6;
			font-size: 15px;
			margin: 20px 0;
			text-align: justify;
			border-radius: 3px;
			padding: 10px;
			color: #757373;
		}

		.content {
			color: #333;
			font-size: 15px;
			text-align: justify;
			line-height: 25px;

			text {
				text-align: center;
			}
		}
	}
</style>
